#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.App {
  width: 90%;
  max-width: 1200px;
  margin: auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.HeaderWrapper {
  margin-bottom: 4rem;
}
.HeaderWrapper a {
  text-decoration: none;
}
.HeaderWrapper h1 {
  text-align: center;
  color: #0B5D72;
}
.HeaderWrapper p {
  text-align: center;
  color: #0B5D72;
}

.FlexWrapper {
  display: flex;
  justify-content: space-between;
  column-gap: 2rem;
  align-items: flex-start;
}

.DropContainer {
  border: 3px dashed #075d72;
  text-align: center;
  background: #075d721f;
  border-radius: 5px;
}
.dropzone {
  padding: 5rem 2rem;
}
.DownloadForm {
  border: 3px solid #075d72;
  padding: 1rem;
  width: 300px;
}


.crop {
  float: left;
  width: 50px;
  margin: .5rem;
  border: 1px solid #000;
}

.square.crop {
  border: 1px solid #000;
}

.crop img {
  display: block;
}

.circle.crop {
  border-radius: 100px;
  overflow: hidden;
}

label {
  float: none;
  clear: both;
  width: 100%;
  display: block;
  position: relative;
  margin-top: 7rem;
}

input[type="text"] {
  width: 100%;
  padding: .5rem 0;
  box-sizing: border-box;
  border: none;
  border-bottom: 2px solid #075d72;
}

span {
  position: absolute;
  bottom: .5rem;
  left: 0rem;
  transition: .5s;
}

input:focus {
  outline: none;
}

input:focus + span {
  left: 0;
  top: -.5rem;
  font-size: .8rem;
  display: none;
}

.download {
  display: block;
  background: #075d72;
  border: none;
  padding: 1rem;
  width: 100%;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  text-align: center;
  margin-top: 4rem;
  box-sizing: border-box;
}

.Footer {
  background: #075d72;
  padding: .5rem;
  text-align: center;
  margin-top: 4rem;
}
.Footer p, .Footer a {
  color: #ffffff;
}